import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getRankConfigs = paramsSearch =>
  request({
    prefix,
    url: `/RankConfig`,
    params: { ...paramsSearch },
    method: 'GET',
  });

export const createRankConfig = (newDoc) =>
  request({
    prefix,
    url: `/RankConfig`,
    data: { ...newDoc },
    method: 'POST',
  });

export const deleteRankConfig = (id) =>
  request({
    prefix,
    url: `/RankConfig`,
    params: { id },
    method: 'DELETE',
  });

export const getRankConfigId = id =>
  request({
    prefix,
    url: `/RankConfig/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateRankConfig = (editDoc) =>
  request({
    prefix,
    url: `/RankConfig`,
    data: { ...editDoc },
    method: 'PUT',
  });

export const downloadFile = (paramsSearch) =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportRankConfigs.xlsx',
    url: `/RankConfig/ExportRankConfigs`,
    data: { ...paramsSearch },
    method: 'POST',
  });



