import React, { Component, Fragment } from 'react';
import { TableData } from 'components/common';
import { getStaffLevelHistories } from 'services/staffProfile';
import moment from 'moment';

class LevelHistory extends Component {
  state = {
    profileId: false,
    reload: 0
  };

  componentWillMount() {
    // console.log("componentWillMount", this.props);
    if(this.props.profileId) {
      this.setState({ profileId: this.props.profileId });
    }
  }

  componentWillReceiveProps(nextProps) {
    // console.log("componentWillReceiveProps", "this.props.profileId", this.props.profileId, "nextProps.profileId", nextProps.profileId);
    if(nextProps.profileId !== this.props.profileId) {
      this.setState({
        profileId: nextProps.profileId,
        reload: this.state.reload + 1,
      });
    }
  }

  render() {
    const columns = [
      {
        title: 'Khung lương áp dụng',
        dataIndex: 'sfc',
        key: 'sfc',
      },
      {
        title: 'Điểm',
        dataIndex: 'point',
        key: 'point',
        align: 'center',
      },
      {
        title: 'Over',
        dataIndex: 'over',
        key: 'over',
        align: 'center',
      },
      {
        title: 'Ngày hiệu lực',
        dataIndex: 'effectDate',
        key: 'effectDate',
        align: 'center',
        render: (text, record) => moment(record.effectDate).format("DD/MM/YYYY")
      },
    ];

    return (
      <Fragment>
        <TableData
          reload={this.state.reload}
          // paramSearch={this.state.paramSearch}
          service={() => getStaffLevelHistories({ id: this.state.profileId })}
          columns={columns}
          // allowGetData={this.state.profileId}
          rowKey="id"
        />  
      </Fragment>
    );
  }
}

export default LevelHistory;



