import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getStaffProfiles = ({
  staffId,
  fullname,
  gender,
  educationType,
  title,
  socialBookNo,
  idCard,
  contractTypeId,
  positionId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/StaffProfile',
    method: 'GET',
    params: {
      staffId,
      fullname,
      gender,
      educationType,
      title,
      socialBookNo,
      idCard,
      contractTypeId,
      positionId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createStaffProfile = data =>
  request({
    prefix,
    url: '/StaffProfile',
    method: 'POST',
    params: {},
    data,
  });

export const updateStaffProfile = ({
  id,
  staffId,
  firstname,
  lastname,
  dob,
  gender,
  ethnicityId,
  recruitedDate,
  townId,
  politicalYear,
  sfcId,
  facultyId,
  subjectGroupId,
  unitId,
  unit,
  educationLevel,
  educationType,
  title,
  afterUniversity,
  universityBachelor,
  collegeBachelor,
  politicalTheory,
  informationTechnology,
  foreignLanguage,
  pedagogicTrain,
  hasOtherCert,
  hasOtherCert2,
  rcId,
  positionId,
  assignedDate,
  currentLevel,
  levelStartDate,
  note,
  contractTypeId,
  contractExpiredDate,
  resignedDate,
  resignAnnouncedDate,
  socialBookNo,
  idCard,
  updatedById,
} = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      staffId,
      firstname,
      lastname,
      dob,
      gender,
      ethnicityId,
      recruitedDate,
      townId,
      politicalYear,
      sfcId,
      facultyId,
      subjectGroupId,
      unitId,
      unit,
      educationLevel,
      educationType,
      title,
      afterUniversity,
      universityBachelor,
      collegeBachelor,
      politicalTheory,
      informationTechnology,
      foreignLanguage,
      pedagogicTrain,
      hasOtherCert,
      hasOtherCert2,
      rcId,
      positionId,
      assignedDate,
      currentLevel,
      levelStartDate,
      note,
      contractTypeId,
      contractExpiredDate,
      resignedDate,
      resignAnnouncedDate,
      socialBookNo,
      idCard,
      updatedById,
    },
  });

export const deleteStaffProfile = ({ id, permanentLeaveDate }) =>
  request({
    prefix,
    url: `/StaffProfile/Delete`,
    method: 'PUT',
    params: { id, permanentLeaveDate },
  });

export const getStaffProfile = ({ id } = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const getStaffProfileUnits = ({
  label,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/StaffProfile/Units',
    method: 'GET',
    params: { label, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getStaffLevelHistories = ({ id } = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/LevelUpHistory`,
    method: 'GET',
    params: {},
    data: {},
  });

export const getStaffDocuments = ({ id } = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/StaffDocuments`,
    method: 'GET',
    params: {},
    data: {},
  });

export const deleteStaffDocument = id =>
  request({
    prefix,
    url: `/StaffProfile/StaffDocuments/${id}/Delete`,
    method: 'DELETE',
    params: {},
    data: {},
  });

export const uploadStaffDocument = ({ id, formData }) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/UploadDocument`,
    method: 'POST',
    params: {},
    data: formData,
  });

export const updateStaffDocument = ({ id, documentId, formData } = {}) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/UpdateDocument/${documentId}`,
    method: 'PUT',
    params: {},
    data: formData,
  });

export const exportOriginalSalaryIncrease = () =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportOriginalSalaryIncrease.xlsx',
    url: '/StaffProfile/ExportOriginalSalaryIncrease',
    data: {},
    method: 'POST',
  });

export const exportRaiseLevelStaff = () =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportRaiseLevelStaff.xlsx',
    url: '/StaffProfile/ExportRaiseLevelStaff',
    data: {},
    method: 'POST',
  });

export const exportBeyondSeniority = () =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportBeyondSeniority.xlsx',
    url: '/StaffProfile/ExportBeyondSeniority',
    data: {},
    method: 'POST',
  });

export const exportStaffProfileExcel = () =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportStaffProfileExcel.xlsx',
    url: '/StaffProfile/ExportStaffProfileExcel',
    data: {},
    method: 'POST',
  });

export const uploadStaffAvatar = ({ id, data }) =>
  request({
    prefix,
    url: `/StaffProfile/${id}/UploadAvatar`,
    method: 'PUT',
    params: {},
    data,
  });

export const getStaffProfilesByAges = ({
  listFilter,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: `/StaffProfile/StaffProfileByAges`,
    method: 'GET',
    params: { 
      ageRangeString: JSON.stringify(listFilter), 
      page,
      pageSize, 
      orderBy, 
      isPagingEnabled 
    },
    data: {}
  });

export const exportStaffProfilesByAgesExcell = listFilter =>
  requestDownload({
    prefix,
    method: 'POST',
    defaultFileName: 'ExportStaffProfileByAgesExcel.xlsx',
    url: '/StaffProfile/ExportStaffProfileByAgesExcel',
    data: {
      ageRanges: listFilter,
    },
  });


