import React, { Component } from 'react';
import { Row, Col, Form, Input, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common';
// import { getContractTypes } from 'service/contractType';
import { getContractTypes } from 'services/contractType';
import { getStaffCategories } from 'services/staffCategory';
// import './style.scss';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

const { Option } = Select;

@Form.create({ name: 'FormSearchStudent' })
class FormSearchStaffProfile extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const {
      form: { getFieldDecorator },
    } = this.props;
    return (
      <Form
        className="form-inline-label search-form"
        layout="vertical"
        onSubmit={this.handleSearch}
      >
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Họ và tên">{getFieldDecorator('fullname', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Mã">{getFieldDecorator('staffId', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Bậc đào tạo">
              {getFieldDecorator('educationLevel', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loại hình đào tạo">
              {getFieldDecorator('educationType', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Danh hiệu cao nhất">
              {getFieldDecorator('title', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Sổ bảo hiểm">
              {getFieldDecorator('SocialBookNo', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="CMND">{getFieldDecorator('idCard', {})(<Input />)}</Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Giới tính">
              {getFieldDecorator('gender', {})(
                <Select>
                  <Option value="" />
                  <Option value="true">Nam</Option>
                  <Option value="false">Nữ</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Loại hợp đồng">
              {getFieldDecorator('contractTypeId', {})(<SelectAjax service={getContractTypes} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Chức vụ">
              {getFieldDecorator('positionId', {})(<SelectAjax service={getStaffCategories} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchStaffProfile;



