import React from 'react';
import { Modal, Form, Row, Col } from 'antd';
import { DatePickerString } from 'components/common';
import { BtnDelete, BtnCancel } from 'components/common/button';

const StaffProfileDeletePopup = props => {
  const { getFieldDecorator } = props.form;

  const onClose = () => {
    props.form.resetFields();
    props.onClose();
  };

  const handleRemove = e => {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (err) return;
      props.onRemove({ ...props.staffSelecting, ...values });
      props.form.resetFields();
    });
  };

  return (
    <Modal
      onOk={handleRemove}
      onCancel={onClose}
      visible={props.visible}
      title={`Xóa hồ sơ ${props.staffSelecting.fullname}`}
      footer={[
        <BtnCancel key="back" onClick={onClose} style={{ height: '40px' }} />,
        <BtnDelete key="submit" onClick={handleRemove} style={{ height: '40px' }} />,
      ]}
    >
      <Form layout="vertical" className="form-inline-label">
        <Row gutter={24}>
          <Col span={24}>
            <Form.Item label=" Thời gian nghỉ việc">
              {getFieldDecorator('permanentLeaveDate', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(
                <DatePickerString style={{ width: '90%' }} placeholder="Chọn thời gian nghỉ việc" />
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

export default Form.create()(StaffProfileDeletePopup);



