import React, { Component } from 'react';
import {  Button, Col, Form, Modal, Input, Row, Upload, Icon, message, Spin } from 'antd';
import { hostAPI } from 'utils';
import { DatePickerString } from 'components/common';
import { uploadStaffDocument, updateStaffDocument } from 'services/staffProfile';
import './style.scss';

@Form.create({ name: 'CertificateForm' })
class CertificateCreateEditPopup extends Component {

  state = {
    file: {},
    loading: false
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.certificate.id && nextProps.certificate.id !== this.props.certificate.id) {
      this.setState({
        file: {
          uid: '-1',
          name: nextProps.certificate.paperName,
          status: 'done',
          url: `${hostAPI}/api/filestorage/Storage/${nextProps.certificate.fileId}/Download`
        }
      });
    }
  }

  onCancel = () => {
    this.props.onClose(false);
    this.props.form.resetFields();
    this.setState({ file: {} })
    // this.props.form.setFieldsValue({ file: [] })
  }

  handleCreateCertificate = e => {
    e.preventDefault();
    const { certificate: { id, fileId} } = this.props;
    this.props.form.validateFields(async(err, formValues) => {
      if (!err) {
        this.setState({ loading: true });
        try {
          if(id) {
            const formData = new FormData();
            formData.append('paperName', formValues.paperName);
            formData.append('paperDate', formValues.paperDate);
            formData.append('staffProfileId', this.props.profileId);
            formData.append('staffDocumentId', fileId);
            formData.append('formFile', formValues.file[0] || null);
            const result = await updateStaffDocument({ id: this.props.profileId, documentId: id, formData });
            if(result.status === 202) {
              message.success('Cập nhật thành công');
              this.props.onClose(true);
              this.props.form.resetFields();
              this.setState({ file: {} })
            }
          } else {
            const formData = new FormData();
            formData.append('formFile', formValues.file[0]);
            formData.append('paperName', formValues.paperName);
            formData.append('paperDate', formValues.paperDate);
            formData.append('id', this.props.profileId);
            const result = await uploadStaffDocument({ id: this.props.profileId, formData });
            if(result.status === 200) {
              message.success('Tải lên thành công');
              this.props.onClose(true);
              this.props.form.resetFields();
              this.setState({ file: {} })
            }
          }
        } catch (error) {
          message.error(`Có lỗi xãy ra ${error}`);
        }
        this.setState({ loading: false });
      }
    });
  }

  normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    if(e.file && e.file.type !== 'application/pdf'
        && e.file.type !== 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
        && !e.file.type.startsWith("image")
      ) {
      message.error('Định dạng không hợp lệ. Vui lòng chọn tập tinh pdf, word và hình ảnh!');
      return e.fileList.length > 0 && e.fileList[0].name !== e.file.name ? [e.fileList[0]] : [];
    }
    return e && [e.file];
  };

  handleRemoveFile = () => {
    this.setState({ file: {} })
    this.props.form.setFieldsValue({ file: [] })
  }

  render() {
    const { file } = this.state;
    const { certificate, form: { getFieldDecorator }} = this.props;
    return (
      <Modal
        title="Tải lên giấy tờ bằng cấp"
        visible={this.props.visible}
        onOk={this.handleCreateCertificate}
        okText="Lưu"
        onCancel={this.onCancel}
        cancelText="Hủy"
        className="certificate-modal"
      >
        <Spin spinning={this.state.loading}>
          <Form>
            <Row>
              <Col span={24}>
                <Form.Item label="Tên giấy tờ">
                  {getFieldDecorator('paperName', {
                    initialValue: certificate.paperName,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng nhập thông tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item label="Ngày cấp">
                  {
                    getFieldDecorator('paperDate', {
                      initialValue: certificate.paperDate,
                      rules: [
                        {
                          required: true,
                          message: 'Vui lòng chọn ngày cấp!',
                        },
                      ],
                    })(<DatePickerString />)
                  }
                </Form.Item>
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <Form.Item label="Giấy tờ bằng cấp">
                  {getFieldDecorator('file', {
                    valuePropName: 'fileList',
                    getValueFromEvent: (fileData) => this.normFile(fileData),
                    initialValue: file.name
                    ? [{
                        uid: '-1',
                        name: certificate.fileName,
                        status: 'done',
                        url: file.url,
                      }]
                    : [],
                    rules: [
                      {
                        required: true,
                        message: 'Vui lòng chọn giấy tờ bằng cấp!',
                      },
                      // {
                      //   validator: this.handleConfirmPassword
                      // }
                    ],
                  })(
                    <Upload
                      name="file"
                      listType="text"
                      onRemove={this.handleRemoveFile}
                      beforeUpload={() => false}
                    >
                      <Button>
                        <Icon type="upload" /> Upload
                      </Button>
                    </Upload>
                  )}
                </Form.Item>
              </Col>
            </Row>
          </Form>
        </Spin>
      </Modal>
    )
  }
}


CertificateCreateEditPopup.defaultProps = {
  certificate: {},
};

export default CertificateCreateEditPopup;



