import React, { Component, Fragment } from 'react';
import { message, Row, Popconfirm, Spin } from 'antd';
import moment from 'moment';
import { hostAPI } from 'utils';
import { TableData } from 'components/common';
import { BtnEdit, BtnDelete, BtnAddNew } from 'components/common/button';
import { getStaffDocuments, deleteStaffDocument } from 'services/staffProfile';
import { getFileInfo } from 'services/fileStorage';
import CertificateCreateEditPopup from './CertificateCreateEditPopup'

class Certification extends Component {
  state = {
    profileId: false,
    reload: 0,
    isPopupVisible: false,
    certificate: {},
    loading: false
  };

  componentWillMount() {
    if(this.props.profileId) {
      this.setState({ profileId: this.props.profileId });
    }
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.profileId !== this.props.profileId) {
      this.setState({
        profileId: nextProps.profileId,
        reload: this.state.reload + 1,
      });
    }
  }

  onCloseCertificateCreateEditPopup = isReload => {
      if(isReload) {
      this.setState({ reload: this.state.reload + 1, isPopupVisible: false, certificate: {} });
    } else {
      this.setState({ isPopupVisible: false, certificate: {} });
    }
  }

  handleDelete = async id => {
    try {
      this.setState({ loading: true });
      await deleteStaffDocument(id);
      message.success('Xóa thành công');
      this.setState({ reload: this.state.reload + 1 });
    } catch (err) {
      message.error(`Có lỗi xãy ra ${err}`);
    }
    this.setState({ loading: false });
  };

  handleEdit = async certificate => {
    try {
      const certificateFile = await getFileInfo(certificate.fileId);
      console.log(certificateFile);
      this.setState({ certificate: {...certificate, ...{ fileName: certificateFile.data.fileName }}, isPopupVisible: true })
    } catch (error) {
      message.error(`Có lỗi xãy ra ${error}`);
    }
  }

  render() {
    const columns = [
      {
        title: 'Tên tài liệu',
        dataIndex: 'paperName',
        key: 'paperName',
        render: (text, record) => <a href={`${hostAPI}/api/filestorage/Storage/${record.fileId}/Download`}>{text}</a>
      },
      {
        title: 'Ngày cấp',
        dataIndex: 'paperDate',
        key: 'paperDate',
        align: 'center',
        render: (text, record) => moment(record.paperDate).format("DD/MM/YYYY")
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <Fragment>
            <BtnEdit
              onClick={() => this.handleEdit(record)}
            />
            <Popconfirm
              title="Đồng ý xóa ?"
              onConfirm={() => this.handleDelete(record.id)}
              okText="Xóa"
              cancelText="Hủy"
            >
              <BtnDelete />
            </Popconfirm>
          </Fragment>
        ),
      },
    ];
    return (
      <Spin spinning={this.state.loading}>
        <Row>
          <BtnAddNew onClick={() => this.setState({ isPopupVisible: true })} />
        </Row>
        <TableData
          reload={this.state.reload}
          service={
            this.state.profileId
            ? () => getStaffDocuments({ id: this.state.profileId })
            : () => ({ data: { items: [], totalResults: 0 } })
          }
          columns={columns}
          rowKey="id"
        />
        <CertificateCreateEditPopup
          visible={this.state.isPopupVisible}
          onClose={this.onCloseCertificateCreateEditPopup}
          certificate={this.state.certificate}
          profileId={this.props.profileId}
        />
      </Spin>
    );
  }
}

export default Certification;



