import React, { Component } from 'react';
import { Col, Row, Form, Input, Checkbox, InputNumber, Select } from 'antd';
import moment from 'moment';

import { requiredRule } from 'utils/validateRules';
import { getEthnicity } from 'services/ethnicity';
import { getFacultys } from 'services/faculty';
import { getListTown } from 'services/town';
import { getRankConfigs } from 'services/rankConfig';
import { getStaffCategories } from 'services/staffCategory';
import { getSalaryFrequencyConfig } from 'services/salaryFrequencyConfigService';
import { getContractTypes } from 'services/contractType';
import { getSubjectGroups } from 'services/subjectGroup';
import { getStaffProfileUnits } from 'services/staffProfile';
import { SelectAjax, DatePickerString } from 'components/common';

const { TextArea } = Input;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    firstname: null,
    lastname: null,
    staffId: null,
    gender: null,
    dob: null,
    idCard: null,
    townId: null,
    ethnicityId: null,
    socialBookNo: null,
    educationLevel: null,
    educationType: null,
    title: null,
    positionId: null,
    unitId: null,
    unit: null,
    afterUniversity: null,
    collegeBachelor: null,
    politicalTheory: null,
    rcId: null,
    facultyId: null,
    subjectGroupId: null,
    informationTechnology: null,
    foreignLanguage: null,
    sfcId: null,
    recruitedDate: null,
    politicalYear: null,
    pedagogicTrain: null,
    assignedDate: null,
    hasOtherCert: null,
    hasOtherCert2: null,
    currentLevel: null,
    levelStartDate: null,
    contractTypeId: null,
    contractExpiredDate: null,
    resignedDate: null,
    resignAnnouncedDate: null,
    note: null,
  };
  return { ...initialValueDefault, ...initialValue };
};

class PersonalInfor extends Component {
  state = {
    initialValue: handleInitialValue({}),
  };

  componentWillReceiveProps(nextProps) {
    if(this.props.profile && this.props.profile.id) {
      const initialValue = handleInitialValue(nextProps.profile);
      this.setState({ initialValue });
    }
  };

  onChangeDob = (value) => {
    const resignedDate = moment(value).add(60, 'years');
    const resignAnnouncedDate = moment(resignedDate).subtract(6, 'months');
    this.props.form.setFieldsValue({
      resignedDate: resignedDate.format('YYYY-MM-DD'),
      resignAnnouncedDate: resignAnnouncedDate.format('YYYY-MM-DD')
    });
  }

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { form: { getFieldDecorator }} = this.props;
    const { initialValue: profile } = this.state;
    const formItemLayout = null;
    return (
      <Form layout="vertical">
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Họ">
              {getFieldDecorator('firstname', { initialValue: profile.firstname, rules: [requiredRule] })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Tên">
              {getFieldDecorator('lastname', { initialValue: profile.lastname, rules: [requiredRule] })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Mã cán bộ">
              {getFieldDecorator('staffId', { initialValue: profile.staffId, rules: [requiredRule] })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Giới tính">
              {getFieldDecorator('gender', { initialValue: profile.gender, rules: [requiredRule], })(
                <Select>
                  <Select.Option value>Nam</Select.Option>
                  <Select.Option value={false}>Nữ</Select.Option>
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày sinh">
              {getFieldDecorator('dob', {
                  initialValue: profile.dob,
                  rules: [requiredRule],
                })(<DatePickerString placeholder="Chọn ngày" onChange={this.onChangeDob} />)
              }
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="CMND">
              {getFieldDecorator('idCard', { initialValue: profile.idCard, rules: [requiredRule] })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Nguyên quán">
              {getFieldDecorator('townId', {
                  initialValue: profile.townId,
                  rules: [requiredRule],
                })(
                  <SelectAjax service={getListTown} />
                )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Dân tộc">
              {getFieldDecorator('ethnicityId', {
                  initialValue: profile.ethnicityId,
                  rules: [requiredRule],
                })(
                  <SelectAjax service={getEthnicity} />
                )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Sổ bảo hiểm">
              {getFieldDecorator('socialBookNo', { initialValue: profile.socialBookNo })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Bậc đào tạo">
              {getFieldDecorator('educationLevel', { initialValue: profile.educationLevel })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Loại hình đào tạo">
              {getFieldDecorator('educationType', { initialValue: profile.educationType })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Danh hiệu cao nhất">
              {getFieldDecorator('title', { initialValue: profile.title })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Chức vụ">
              {
                getFieldDecorator('positionId', {
                  initialValue: profile.positionId,
                })(
                  <SelectAjax service={getStaffCategories} />
                )
              }
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Đơn vị trực thuộc">
              {
                getFieldDecorator('unitId', {
                  initialValue: profile.unitId,
                })(
                  <SelectAjax service={getStaffProfileUnits} />
                )
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Cử nhân đại học">
              {getFieldDecorator('universityBachelor', { initialValue: profile.unit })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Sau đại học">
              {getFieldDecorator('afterUniversity', { initialValue: profile.afterUniversity })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Cử nhân cao đẳng">
              {getFieldDecorator('collegeBachelor', { initialValue: profile.collegeBachelor })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Lý luận chính trị">
              {getFieldDecorator('politicalTheory', { initialValue: profile.politicalTheory })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Mã ngạch">
              {
                getFieldDecorator('rcId', {
                  initialValue: profile.rcId,
                  rules: [requiredRule],
                })(
                  <SelectAjax service={getRankConfigs} />
                )
              }
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Khoa">
              {getFieldDecorator('facultyId', {
                  initialValue: profile.facultyId,
                  // rules: [requiredRule],
                })(
                  <SelectAjax service={getFacultys} />
                )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Bộ môn">
              {getFieldDecorator('subjectGroupId', {
                  initialValue: profile.subjectGroupId,
                  // rules: [requiredRule],
                })(
                  <SelectAjax service={getSubjectGroups} />
                )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Tin học">
              {getFieldDecorator('informationTechnology', { initialValue: profile.informationTechnology })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngoại ngữ">
              {getFieldDecorator('foreignLanguage', { initialValue: profile.foreignLanguage })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Khung lương áp dụng">
              {getFieldDecorator('sfcId', {
                  initialValue: profile.sfcId,
                  rules: [requiredRule],
                })(
                  <SelectAjax service={getSalaryFrequencyConfig} keyLabel="fullInfo" />
                )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày tuyển dụng">
              {getFieldDecorator('recruitedDate', { initialValue: profile.recruitedDate, rules: [requiredRule] })(<DatePickerString />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Năm vào đảng">
              {getFieldDecorator('politicalYear', { initialValue: profile.politicalYear })(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Bồi dưỡng nghiệp vụ sư phạm">
              {getFieldDecorator('pedagogicTrain', { initialValue: profile.pedagogicTrain })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày bổ nhiệm">
              {getFieldDecorator('assignedDate', { initialValue: profile.assignedDate })(<DatePickerString />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('hasOtherCert', { initialValue: profile.hasOtherCert })
              (<Checkbox>GD học ĐH cho GV ĐH, CĐ</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout}>
              {
                getFieldDecorator('hasOtherCert2', { initialValue: profile.hasOtherCert2 })
                (<Checkbox>Quản lý NN- Chương trình chuyên viên</Checkbox>)
              }
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Bậc hiện tại">
              {getFieldDecorator('currentLevel', { initialValue: profile.currentLevel, rules: [requiredRule] })(<InputNumber />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày hưởng bậc">
              {getFieldDecorator('levelStartDate', { initialValue: profile.levelStartDate, rules: [requiredRule] })(<DatePickerString />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Loại hợp đồng">
              {getFieldDecorator('contractTypeId', { initialValue: profile.contractTypeId, rules: [requiredRule] })(
                <SelectAjax service={getContractTypes} />
              )}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày hết hạn hợp đồng">
              {getFieldDecorator('contractExpiredDate', { initialValue: profile.contractExpiredDate })(<DatePickerString />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày nghỉ hưu dự kiến">
              {getFieldDecorator('resignedDate', { initialValue: profile.resignedDate })(<DatePickerString />)}
            </Form.Item>
          </Col>
          <Col span={12}>
            <Form.Item {...formItemLayout} label="Ngày thông báo nghỉ hưu dự kiến">
              {getFieldDecorator('resignAnnouncedDate', { initialValue: profile.resignAnnouncedDate })(<DatePickerString />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Form.Item {...formItemLayout} label="ghi chú">
              {getFieldDecorator('note', { initialValue: profile.note })(<TextArea rows={4} />)}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default PersonalInfor;



