/* eslint-disable */
import React from 'react';
import { Button, Col, Tabs, Form, Row } from 'antd';
import './style.scss';
import PersonalInfor from './PersonalInfor';
import Certification from './Certification';
import LevelHistory from './LevelHistory';
import AvatarUpload from './AvatarUpload';

const { TabPane } = Tabs;

@Form.create({ name: 'ProfileForm' })
class ProfileApp extends React.Component {
  state = {
    activeTab: "PERSONAL_INFORMATION_TAB",
    avatarFile: false
  };

  componentWillReceiveProps(nextProps) {
    if(nextProps.isPopupClose && nextProps.isPopupClose !== this.props.isPopupClose) {
      this.setState({ activeTab: "PERSONAL_INFORMATION_TAB" });
    }
  }

  updateAvatarFile = file => {
    this.setState({ avatarFile: file });
  }

  render() {
    const { activeTab } = this.state;
    const { profile, form, isPopupClose } = this.props;
    return (
      <Form className="profile" layout="vertical">
        <Row>
          <Col xl={8}>
            <div className="card profile__header" style={{ backgroundImage: `url(resources/images/photos/4.jpeg)` }}>
              <div className="profile__header-card">
                <div className="card-body text-center">
                  <AvatarUpload
                    profileId={profile.id}
                    imgStorageId={profile.avatarFileId}
                    updateAvatarFile={this.updateAvatarFile}
                    isReset={!isPopupClose}
                  />
                  <br />
                  <br />
                  <Button.Group size="default">
                    <Button style={{ width: 150 }}>{profile.fullname}</Button>
                    <Button style={{ width: 150 }}>MSCB: {profile.staffId}</Button>
                  </Button.Group>
                  <br />
                </div>
              </div>
            </div>
          </Col>
          <Col xl={16}>
            <div className="card">
              <div className="card-body">
                <Tabs
                  defaultActiveKey="PERSONAL_INFORMATION_TAB"
                  activeKey={activeTab}
                  onTabClick={tab => this.setState({ activeTab: tab })}
                  className="personal-information-tab"
                >
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-menu" /> Thông tin cá nhân
                      </span>
                    }
                    key="PERSONAL_INFORMATION_TAB"
                  >
                    <PersonalInfor profile={profile || {}} form={form} />
                  </TabPane>
                  <TabPane
                    tab={
                      <span>
                        <i className="icmn-profile" /> Giấy tờ bằng cấp
                      </span>
                    }
                    key="CERTIFICATE_TAB"
                  >
                    <Certification profileId={profile.id} />
                  </TabPane>
                  { profile.id &&
                    <TabPane
                      tab={
                        <span>
                          <i className="icmn-history" /> Lịch sử tăng lương
                        </span>
                      }
                      key="LEVEL_HISTORY_TAB"
                    >
                      <LevelHistory profileId={profile.id} />
                    </TabPane>
                  }
                </Tabs>
              </div>
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default ProfileApp;



