import { request } from 'utils';

const prefix = '/api/organization';

export const getContractTypes = ({ page, pageSize=10, orderBy, isPagingEnabled=true } = {}) =>
  request({
    prefix,
    url: '/ContractType',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {}
  });


