import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew, BtnEdit, BtnDelete, BtnExport } from 'components/common/button';
import { CartHeadAction, TableData } from 'components/common';
import { Card, Row, Col, message, Spin } from 'antd';
import {
  getStaffProfiles,
  getStaffProfile,
  deleteStaffProfile,
  exportOriginalSalaryIncrease,
  exportRaiseLevelStaff,
  exportBeyondSeniority,
  exportStaffProfileExcel,
} from 'services/staffProfile';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchStaffProfile from './FormSearchStaffProfile';
import StaffProfileEditPopup from './StaffProfileEditPopup';
import StaffProfileDeletePopup from './StaffProfileDeletePopup';

class StaffProfile extends Component {
  state = {
    paramSearch: {},
    reload: 0,
    profileData: {},
    showEditPopup: false,
    showDeletePopup: false,
    staffSelecting: {},
    loading: false,
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  onAddNew = () => {
    this.setState({
      showEditPopup: true,
      profileData: {},
    });
  };

  onEdit = async id => {
    try {
      this.setState({ loading: true });
      const result = await getStaffProfile({ id });
      if (result.data) {
        this.setState({
          profileData: result.data,
          showEditPopup: true,
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  onDelete = staff => {
    this.setState({ staffSelecting: staff, showDeletePopup: true });
  };

  onRemove = async staff => {
    try {
      this.setState({ showDeletePopup: false, loading: true });
      await deleteStaffProfile(staff);
      message.success(`Xóa ${staff.fullname} thành công`);
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false, staffSelecting: {} });
    }
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleExportSalaryIncrease = async () => {
    try {
      this.setState({ loading: true });
      await exportOriginalSalaryIncrease();
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  handleExportRaiseLevelStaff = async () => {
    try {
      this.setState({ loading: true });
      await exportRaiseLevelStaff();
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  handleBeyondSeniority = async () => {
    try {
      this.setState({ loading: true });
      await exportBeyondSeniority();
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  handleExportStaffProfile = async () => {
    try {
      this.setState({ loading: true });
      await exportStaffProfileExcel();
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({ loading: false });
    }
  };

  columns = [
    {
      title: 'Mã',
      dataIndex: 'staffId',
      key: 'staffId',
    },
    {
      title: 'Họ và tên',
      dataIndex: 'fullname',
      key: 'fullname',
    },
    {
      title: 'Ngày sinh',
      dataIndex: 'dob',
      key: 'dob',
      align: 'center',
    },
    {
      title: 'Giới tính',
      dataIndex: 'genderText',
      key: 'genderText',
      align: 'center',
    },
    {
      title: 'Ngày tuyển dụng',
      dataIndex: 'recruitedDate',
      key: 'recruitedDate',
      align: 'center',
    },
    {
      title: 'Bậc đào tạo',
      dataIndex: 'educationLevel',
      key: 'educationLevel',
      align: 'center',
    },
    {
      title: 'Danh hiệu cao nhất',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Chức vụ',
      dataIndex: 'position',
      key: 'position',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      className: 'action-column',
      align: 'center',
      render: (text, record) => (
        <Fragment>
          <BtnEdit onClick={() => this.onEdit(record.id)} />
          <BtnDelete onClick={() => this.onDelete(record)} />
        </Fragment>
      ),
    },
  ];

  render() {
    const { showEditPopup, showDeletePopup, staffSelecting, profileData } = this.state;

    return (
      <Fragment>
        <StaffProfileEditPopup
          profile={profileData}
          visible={showEditPopup}
          onClose={() => this.setState({ showEditPopup: false })}
          reloadProfile={() => this.setState({ reload: this.state.reload + 1 })}
        />
        <StaffProfileDeletePopup
          staffSelecting={staffSelecting}
          visible={showDeletePopup}
          onClose={() => this.setState({ showDeletePopup: false, staffSelecting: {} })}
          onRemove={this.onRemove}
        />
        <Spin spinning={this.state.loading}>
          <Card title="Danh mục chức vụ">
            <CartHeadAction>
              <BtnExport
                className="btnExport"
                title="Xuất danh sách nâng lương gốc"
                onClick={this.handleExportSalaryIncrease}
              />
              <BtnExport
                className="btnExport"
                title="Xuất thâm niên"
                onClick={this.handleExportRaiseLevelStaff}
              />
              <BtnExport
                className="btnExport"
                title="Xuất vượt khung"
                onClick={this.handleBeyondSeniority}
              />
              <BtnExport
                className="btnExport"
                title="Xuất danh sách cán bộ theo đơn vị"
                onClick={this.handleExportStaffProfile}
              />
            </CartHeadAction>
            <Row>
              <Col span={24}>
                <FormSearchStaffProfile />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <BtnAddNew onClick={this.onAddNew} />
              </Col>
            </Row>
            <Row>
              <Col span={24}>
                <TableData
                  paramSearch={this.state.paramSearch}
                  reload={this.state.reload}
                  service={getStaffProfiles}
                  columns={this.columns}
                  rowKey="id"
                />
              </Col>
            </Row>
          </Card>
        </Spin>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <StaffProfile />
  </Page>
);



