import React from 'react';
import { Button, Upload, Icon, message, Spin } from 'antd';
import { hostAPI } from 'utils';
import { uploadStaffAvatar } from 'services/staffProfile';
import './style-avatar.scss';

function beforeUpload(file) {
  const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
  if (!isJpgOrPng) {
    message.error('You can only upload JPG/PNG file!');
  }
  const isLt2M = file.size / 1024 / 1024 < 2;
  if (!isLt2M) {
    message.error('Image must smaller than 2MB!');
  }
  return isJpgOrPng && isLt2M;
}

class AvatarUpload extends React.Component {
  state = {
    loading: false,
    imageUrl: false
  };

  componentWillMount() {
    this.setState({
      imageUrl: this.props.imgStorageId
        ? `${hostAPI}/api/filestorage/Storage/${this.props.imgStorageId}/Download`
        : false
    });
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.imgStorageId !== this.state.imgStorageId) {
      this.setState({ 
        imageUrl: `${hostAPI}/api/filestorage/Storage/${nextProps.imgStorageId}/Download`
      });
    }
    if(nextProps.isReset) {
      this.setState({ imageUrl: false })
    }
  }

  onChangeAvatar = () => {
    console.log("onChangeAvatar");
  }

  handleUpload = async file => {
    const { profileId, updateAvatarFile } = this.props;
    this.setState({ loading: true });
    try {
      if(!profileId) {
        this.setState({ imageUrl: URL.createObjectURL(file) });
        updateAvatarFile(file);
      } else {
        const formData = new FormData();
        formData.append('formFile', file);
        formData.append('id', profileId);
        const result = await uploadStaffAvatar({ id: profileId, data: formData })
        if(result.status === 200 && result.data) {
          this.setState({
            imageUrl: `${hostAPI}/api/filestorage/Storage/${result.data.id}/Download`
          });
        } else {
          message.error('Có lỗi xãy ra!');
        }
      }
    } catch (error) {
      message.error('Có lỗi xãy ra!');
    }
    this.setState({ loading: false });
  }

  render() {
    const uploadButton = (
      <div>
        <Icon type={this.state.loading ? 'loading' : 'plus'} />
        <div className="ant-upload-text">Upload</div>
      </div>
    );
    return (
      <Spin spinning={this.state.loading} className="staff-avatar">
        <Upload
          name="avatar"
          listType="picture-card"
          className="avatar-uploader"
          showUploadList={false}
          action={this.handleUpload}
          beforeUpload={beforeUpload}
        >
          <Button className={`btn-select ${this.state.imageUrl ? '' : 'empty-avatar'}`}>
            <Icon type="upload" />
            <span>Chỉnh sửa</span>
          </Button>
          {this.state.imageUrl ? <img src={this.state.imageUrl} alt="avatar" style={{ width: '100%' }} /> : uploadButton}
        </Upload>
        {/* <div className="wrapper">
          <div className="edit-hover" role="button" tabIndex="0" onClick={this.onChangeAvatar} onKeyDown={this.onChangeAvatar}>
            <Icon type="edit" />
          </div>
        </div> */}
      </Spin>
    );
  }
}

export default AvatarUpload;


