import React, { Component } from 'react';
import { Modal, message, Spin } from 'antd';
import { createStaffProfile, updateStaffProfile } from 'services/staffProfile';
import Profile from './Profile';

class StaffProfileEditPopup extends Component {
  state = {
    loading: false,
  };

  onClose = () => {
    this.props.onClose();
    this.formRef.props.form.resetFields();
  };

  saveFormRef = formRef => {
    this.formRef = formRef;
  };

  handleCreate = async () => {
    const {
      form,
      profile: { id },
    } = this.formRef.props;
    const { avatarFile } = this.formRef.state;
    form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      try {
        this.setState({ loading: true });
        let result = {};
        if (id) {
          result = await updateStaffProfile({ ...values, id });
        } else {
          const formData = new FormData();
          Object.entries(values).forEach(([key, value]) => {
            if (value !== null) formData.append(key, value);
          });
          formData.append('formFile', avatarFile);
          result = await createStaffProfile(formData);
        }
        if (result.status === 200) {
          this.onClose();
          message.success(id ? 'Cập nhật thành công!' : 'Thêm thành công!');
        } else {
          message.error('Có lỗi xãy ra');
        }
        form.resetFields();
        this.props.reloadProfile();
        this.setState({ loading: false });
      } catch (error) {
        console.log(error);
        this.setState({ loading: false });
      } finally {
        this.setState({ loading: false });
      }
    });
  };

  render() {
    return (
      <Modal
        title=""
        visible={this.props.visible}
        onOk={this.handleCreate}
        okText="Lưu"
        onCancel={this.onClose}
        cancelText="Hủy"
        width={1200}
      >
        <Spin spinning={this.state.loading}>
          <Profile
            profile={this.props.profile || {}}
            isPopupClose={this.props.visible}
            wrappedComponentRef={this.saveFormRef}
          />
        </Spin>
      </Modal>
    );
  }
}

export default StaffProfileEditPopup;



